import { Component, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { LocalityService } from "src/app/shared/locality.service";

@Component({
  selector: "app-locality",
  templateUrl: "./locality.component.html",
  styleUrls: ["./locality.component.scss"]
})
export class LocalityComponent implements OnInit {
  map: boolean;
  set: boolean;
  msg: any;
  areas = [];
  area_list: any;
  cities = [];
  city_list: any;
  location_keys = [];
  location: any;

  loc = {
    state: "",
    city: "",
    area: "",
    pin: ""
  };
  positions: any;
  loader = false;
  constructor(private locs: LocalityService) {}

  ngOnInit() {
    this.loader = true;
    this.locServ();
  }
  getGeo() {
    this.map = true;
    setTimeout(() => {
      this.map = false;
      this.locs.getPosition().then(data => {
        console.log(data);
        this.positions = data;
      });
      // if (navigator.geolocation) {
      //   navigator.geolocation.getCurrentPosition(displayLocationInfo);
      //   this.map = false;
      // }
      // function displayLocationInfo(position) {
      //   const lng = position.coords.longitude;
      //   const lat = position.coords.latitude;
      //   console.log(`longitude: ${lng} | latitude: ${lat}`);
      // }
    }, 4000);
  }
  locServ() {
    this.locs.getlocality().subscribe(
      data => {
        this.loader = false;
        this.location = data;
        // tslint:disable-next-line:forin
        for (const key in this.location) {
          this.location_keys.push(key);
        }
      },
      err => {}
    );
  }
  stateset(state) {
    this.cities = [];
    this.loc.city = "";
    this.loc.area = "";
    this.loc.pin = "";
    this.city_list = this.location[state];
    // tslint:disable-next-line:forin
    for (const key in this.city_list) {
      this.cities.push(key);
    }
  }
  cityset(statecity) {
    this.loc.area = "";
    this.loc.pin = "";
    this.areas = [];
    this.areas = this.city_list[statecity];
    // tslint:disable-next-line:forin
  }
  setLoc() {
    this.set = true;
    window.localStorage.setItem("location", JSON.stringify(this.loc));
    setTimeout(() => {
      this.set = false;
      this.eat("Location setted", 1);
    }, 5000);
  }
  setGeo() {
    this.set = true;
    window.localStorage.setItem("geolocation", JSON.stringify(this.positions));
    setTimeout(() => {
      this.set = false;
      this.eat("Geo Location setted", 1);
    }, 5000);
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function() {
      x.className = x.className.replace("show", "");
    }, 5000);
  }
}
