import { Component, OnInit } from "@angular/core";
import { LocalityService } from "src/app/shared/locality.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-shift",
  templateUrl: "./shift.component.html",
  styleUrls: ["./shift.component.scss"]
})
export class ShiftComponent implements OnInit {
  regData: string;
  reg: boolean;
  logdata: boolean;
  info: any;
  msg = "";
  enter = true;
  constructor(private loc: LocalityService, private router: Router) {}
  login = {
    username: "",
    password: "",
    phrase: "",
    lucky: 0
  };
  cpassword = "";
  ngOnInit() {
    this.loc.getYellow().subscribe(data => {
      this.info = data;
      console.log(this.info.length);
    });
  }
  logins() {
    delete this.login.lucky;
    delete this.login.phrase;
    this.enter = false;
    this.logdata = true;
    setTimeout(() => {
      for (let index = 0; index < this.info.length; index++) {
        const element = this.info[index];
        if (element === btoa(JSON.stringify(this.login))) {
          this.logdata = false;
          this.eat("Login Success", 1);
          window.localStorage.setItem("logged", element);
          window.localStorage.setItem("state", "true");
          setTimeout(() => {
            // this.router.navigate(["/lander"]);
          }, 3000);
        } else {
          if (index === this.info.length - 1) {
            this.enter = true;
            this.logdata = undefined;
            this.eat("Login failed", 0);
          }
        }
      }
    }, 5000);
  }
  register() {
    this.enter = false;
    delete this.login.lucky;
    delete this.login.phrase;
    this.regData = btoa(JSON.stringify(this.login));
    this.logdata = true;
    setTimeout(() => {
      this.logdata = undefined;
      this.enter = true;
      this.reg = true;
      this.eat("Registeration successful", 1);
    }, 2000);
  }
  eat(msg, color) {
    this.msg = msg;
    const x = document.getElementById("snackbar");
    if (color === 1) {
      x.style.backgroundColor = "#9ccc65";
    } else if (color === 2) {
      x.style.backgroundColor = "#ffc107";
    } else {
      x.style.backgroundColor = "#ef5350";
    }
    x.className = "show";
    setTimeout(function() {
      x.className = x.className.replace("show", "");
    }, 5000);
  }
}
