import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-unshift",
  templateUrl: "./unshift.component.html",
  styleUrls: ["./unshift.component.scss"]
})
export class UnshiftComponent implements OnInit {
  reset = {
    username: "",
    old: "",
    new: "",
    re: ""
  };
  load = true;
  constructor() {}

  ngOnInit() {
    this.load = true;
    setTimeout(() => {
      this.load = false;
    }, 3000);
  }
}
